import React from 'react';
import ReactDOM from 'react-dom';
import {
    Page001,
    Page002,
    Page003,
    Page004
} from './index';

const components = {
    Page001: Page001,
    Page002: Page002,
    Page003: Page003,
    Page004: Page004
};

function Page({dimension, currentPage}) {
    const pageNumber = Intl.NumberFormat("en-US", {
        minimumIntegerDigits: 3
    }).format(currentPage);

    let PageTag = components["Page" + pageNumber];

    return(<PageTag height="80%" />);

}

export default Page;
